
.timer{
    margin: 50px auto;
    text-align: center;
}
.timer input{
    font-size: 7rem;
    font-family: sans-serif;
    width: 400px;
    border: 3px solid black;
    padding: 2px;
    text-align: center;
}
.timer p{
    font-size: 7rem;
    font-family: sans-serif;
    border: 3px solid black;
    padding: 2px;
    width: 400px;
    margin: auto;
}
.timer button{
    display: block;
    margin: 20px auto;
    background-color: #222;
    color:#ddd;
    width: 200px;
    height: 200px;
    border-radius: 100%;
    cursor: pointer;
    font-size: 3rem;
}
.timer button:hover{
    background-color: #000;
    color: #fff;
    transition: 1s;
}
.timer button:focus{
    outline: none;
}