.pay-and-cry{
    width: 600px;
    height: 250px;
    margin: 50px auto;
    border: solid black 1px;
}
.payandcry-form{
    width: 600px;
    height: 250px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 70px 1fr;
    grid-template-areas: "h h"
                         "t i"
}
.payandcry-form h2{
    text-align: center;
    grid-area: h;
}
.payandcry-text{
    grid-area: t;
}
.payandcry-inputs{
    grid-area: i;
    text-align: right;
}
.payandcry-text p{
    margin:  6px 20px;
}
.payandcry-inputs input{
    margin: 3px 20px;
    width: 250px;
}
