.searchform{
    width: 700px;
    margin: 30px auto;
    display: flex;
    background-color: rgb(255, 238, 143);
    padding: 6px 0;
    border-radius: 5px;
}
.searchform p{
    cursor: pointer;
    background-color:rgb(255, 238, 143);
    padding: 2px 15px;
    border-radius: 4px;
    margin: 0px;
    font-size: 1.2rem;
}
.inpcontainer{
    position: relative;
    margin-left: 5px;
}
#inp{
    width: 600px;
    position: absolute;
    top: 0;
    bottom: 0;
    height: 20px;
    margin: auto;
    z-index: 10;
}
#inp:focus{
    outline: 1px;
}
.res{
    position: absolute;
    top: 23px;
    width: 600px;
    background-color: white;
    box-shadow: 0 -2px 2px gray;
    display: none;
    z-index: 10;
}
.res ul{
    list-style-type: none;
    padding: 0px;
    margin: 0px;
}
.res ul li{
    padding: 4px 4px;
    cursor: pointer;
    font-weight: bold;
}
.res ul li:hover{
    background-color:rgb(255, 238, 143);
}
.res div{
    position: relative;
    display: inline-block;
    width: 100%;
}
.res span{
    position:absolute;
    right: 5px;
    color: rgb(187, 187, 187);
}
.res span:hover{
    color: rgb(58, 58, 58);
    cursor: pointer;
}
.test{
    width: 700px;
    margin: 20px auto;
}