
.check-container{
    display: flex;
    margin: 10px;
    margin: 50px auto;
    width: 250px;
    font-size: 1.5rem;
}
.check{
    color: rgb(138, 138, 138);
    width: 45px;
    height: 45px;
    cursor: pointer;
    font-size: 2.5rem;
}
.checked{
    color: rgb(235, 196, 21);
}