/*Header*/
.app-container{
    margin: 0 auto;
}
header{
    height: 45px;
    background-color: rgb(66, 66, 66);
    position: sticky;
    top:0px;
    z-index: 100;
}
nav{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 1024px;
    margin: 0 auto;
}
nav ul{
    background-color: rgb(66, 66, 66);
}
nav p:hover{
    background-color: rgb(37, 37, 37);
}
nav p, nav a{
    height: 45px;
    padding: 10px;
    text-align: center;
    cursor: pointer;
    text-decoration: none;
    font-size: 1.4rem;
    color: rgb(255, 255, 255);
    text-transform: uppercase;
}
nav li{
    list-style-type: none;
    padding: 10px;
    text-align: center;
    display: grid;
    height: 45px;
}
#navul li:hover{
    background-color: rgb(37, 37, 37);
}
#navul a{
    padding: 0px;
}
/* Main */
.main{
    min-width: 1024px;
    
}
.content{
    width: 80%;
    min-width: 1024px;
    margin: 0 auto;
}



