
.cont{
    width: max-content;
    margin: 100px auto;
    padding: 50px;
    border: solid 2px black;
    display: flex;
    flex-direction: row;
    justify-content: top;
}
.chekcont{
    margin: 0 20px;
}
.main{
    display: block;
}
#tree ul{
    display: none;
}
.chekcont span{
    color: gray;
}
.cont input, .cont span{
    cursor: pointer;
}
.cont li{
    list-style-type: none;
    margin-left: 20px;
}
.chekbox{
    margin: 10px 0;
}